.header{
  height: 100px;
  width: 100%;
  background-color : #E9EEF6;
  padding-top: 10px;
}

.card-container{
  height: 300px;
  width: 300px;
  box-shadow: 0px 3px 50px #A5A5A5;
}

.card-wrapper{
  width: 270px; 
  height: 230px; 
  box-shadow: 0px 3px 50px #A5A5A5;
  display: flex;
  flex-direction: column;
  margin:8px;
}

.card-top{
  width: 100%;
  height: 2%;
}

.task-holder{
  width: 100%;
  height: 98%;
  padding: 10px 10px;
  display : flex;
  flex-direction: column;
  position: relative;
}

.card-header{
 font-weight: bold;
 text-transform: capitalize;
  padding: 1px 1px !important;
  text-align: center;
}

.card-header1{
  font-weight:normal;
  text-transform: capitalize;
   padding: 1px 1px !important;
   text-align:justify;
 }
 

.task-container{
  height: 600px;
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  background-color: #F6F7F8;
  padding : 10px 100px;
}